import React, { ComponentType, forwardRef } from 'react'
import { EuiButtonProps } from '@elastic/eui/src/components/button/button'
import { EuiButton } from '@elastic/eui'

const Button: ComponentType<EuiButtonProps> = forwardRef<typeof EuiButton, EuiButtonProps>((props, ref) => (
  // @ts-ignore
  <EuiButton {...props} buttonRef={ref}>
    {props.children}
  </EuiButton>
))

Button.displayName = `forwardRef(EuiButton)`

export default Button
